import * as msal from "@azure/msal-browser";
import * as msalC from "@azure/msal-common";
import * as teams from "@microsoft/teams-js";

const appId = "890c8d10-4bfd-4103-8a8d-8135accaee37"; // MSALtest
const scopes = [
    "User.Read", // read user profile
    "User.Read.All", // read other users' profiles
    "Mail.Read", // read user's mail
    "ChannelMessage.Read.All", // obviously, read messages in a channel
    "Group.Read.All", // Enumerate teams (Team = Group) and team information
    "Presence.Read.All", // Read users' presence state
    "Directory.Read.All", // members of a team
    "Group.ReadWrite.All", // post to a channel? create channels? seems to do a lot
];

export const loginHintKey = "msalLoginHint";

const config: msal.Configuration = {
    auth: {
        clientId: appId,
        authority: "https://login.microsoftonline.com/common", // default is common, others:  https://login.microsoftonline.com/<tenant>  https://login.microsoftonline.com/organizations/  https://login.microsoftonline.com/consumers/
        redirectUri: window.location.origin + "/auth-end.html",
        navigateToLoginRequestUrl: false,
    },
    cache: {
        cacheLocation: "localStorage"
    },
};


export const inTeams = new Promise<boolean>((resolve, reject) => {
    try {
        teams.initialize(() => resolve(true));
        setTimeout(() => resolve(false), 1000);
    } catch (e) {
        reject(e);
    }
});


export function getContext(): Promise<teams.Context> {
    return new Promise((resolve, reject) => {
        try {
            teams.getContext(resolve);
            setTimeout(() => resolve(undefined), 1000);
        } catch (err) {
            reject(err);
        }
    });
}


export async function setLoginHint(hint: string) {
    if (await inTeams) {
        localStorage.setItem(loginHintKey, hint);
    }
}


export const getLoginHint = async (): Promise<string | null> => {
    if (await inTeams) {
        const ctx = await getContext();
        return ctx && ctx.loginHint || null;
    }

    return localStorage.getItem(loginHintKey);
};


export const client = new msal.PublicClientApplication(config);


export function getAuthParameters(hint?: string | null): msal.AuthenticationParameters {
    const params: msal.AuthenticationParameters = {
        scopes: [...scopes],
    };

    if (hint) {
        params.loginHint = hint;
    }

    return params;
}


export function getRenewParameters(hint?: string | null): msalC.TokenRenewParameters {
    const params: msalC.TokenRenewParameters = {
        scopes: [appId, ...scopes],
        forceRefresh: false, // Set this to "true" to skip a cached token and go to the server to get a new token
    };

    if (hint) {
        params.loginHint = hint;
    }

    return params;
}



